.tabBar {
  display: flex;
  background-color: #191a20;
  height: 48px;
  align-items: center;
}

.tab {
  width: 200px; 
  padding: 0 16px;
  color: #88e3f7;
  text-decoration: none;
  background-color: #21222c;
  border-right: 1px solid #18191f;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 17px;
  transition: background-color 0.2s ease;
  box-sizing: border-box;
}

.activeTab {
  width: 200px; 
  padding: 0 16px;
  color: #88e3f7;
  text-decoration: none;
  border-top: 1px solid #ff79c6;
  background-color: #44475a;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 17px;
  transition: background-color 0.2s ease;
  box-sizing: border-box;
}
