.projectsContainer {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding: 40px;
  background-color: #44475a;
  color: #f8f8f2; 
}

.projectCard {
  background-color: #525569; 
  border-radius: 8px;
  border-color: #282a36;
  border-width: 3px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
  transition: transform 0.2s ease-in-out;
}

.projectCard:hover {
  transform: translateY(-5px); 
}

.projectImage {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
}

.projectHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.projectTitle {
  font-size: 2rem;
  color: #bd93f9; 
  margin-bottom: 5px;
}

.projectSubtitle {
  font-size: 1.2rem;
  color: #f1fa8c; 
  margin-bottom: 15px;
}

.projectDescription {
  font-size: 1rem;
  color: #f8f8f2; 
  line-height: 1.6;
  margin-bottom: 20px;
}

.projectTags {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.projectTag {
  background-color: #88e3f7;
  color: #282a36; 
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.projectTag:hover {
  background-color: #ffb86c;
}

.gitIcon {
  margin-top: 50px;
  color: white; 
}

.gitIcon:hover {
  color: #8be9fd;
}