@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";

.container {
  background-color: #44475a;
  color: #f8f8f3;
  font-family: 'Segoe UI', Tahoma, sans-serif;
  padding: 80px;
}

.topSection {
  display: flex;
  flex-direction: row;
  gap: 40px;
  margin-bottom: 4vh;
}

.carouselContainer {
  width: 100%;
  max-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.carousel {
  width: 100%;
}

.left {
  flex: 1;
}

.right {
  flex: 1;
  display: flex;
  justify-content: center; 
  align-items: center;    
}

.portraitWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 5vh;
}

.portrait {
  width: 350px;
  height: 350px;
  object-fit: cover;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.caption {
  margin-top: 12px;
  font-size: 0.95rem;
  color: #aaa;
}

.title {
  font-size: 2rem;
  color: f8f8f3;
  margin-bottom: 8vh;
}

.subtitle {
  font-size: 1rem;
  line-height: 1.6;
}

.sectionTitle {
  color: f8f8f3;
  margin-bottom: 1rem;
}

.timelineWrapper {
  display: left;
  flex-direction: column;
  align-items: center;
}

.timelineHeading {
  font-size: 1.5rem;
  color: white;
  margin-bottom: 2rem;
}

.timeline {
  position: relative;
  max-width: 600px;
  width: 100%;
  padding-left: 20px;
  border-left: 2px solid white;
}

.timelineItem {
  position: relative;
  margin-bottom: 40px;
}

.timelineDot {
  position: absolute;
  left: -9px;
  top: 5px;
  width: 12px;
  height: 12px;
  background-color: #007acc;
  border-radius: 50%;
  border: 2px solid #1e1e1e;
}

.timelineContent {
  padding-left: 20px;
}

.timelineContent h3 {
  color: white;
  margin: 0 0 4px;
  font-size: 1.1rem;
}

.timelineContent h4 {
  color: white;
  margin: 0 0 4px;
  font-size: 0.9rem;
}


.date {
  font-size: 0.9rem;
  color: #888;
  margin-bottom: 8px;
  display: inline-block;
}

.timelineDetails {
  font-size: 0.9rem;
  color: #bbb;
  margin-top: 5px;
}

.highlightOrange {
  color: #ffb86c; 
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.highlightRed {
  color: #ff5555; 
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.highlightYellow {
  color: #f1fa8c; 
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.highlightGreen {
  color: #ff79c6; 
  margin-bottom: 2rem;
  margin-top: 2rem;
}


.highlightTitle {
  color: #bd93f9; 
  font-size: 2rem;
  margin-bottom: 2.5rem;
  margin-top: 15vh;
}