.container {
  display: flex;
  padding: 100px 80px 80px;
  background-color: #44475a;
  color: #cccccc;
  font-family: 'Segoe UI', Tahoma, sans-serif;
}

.left {
  flex: 1;
  padding-right: 40px;
}

.right {
  margin-top:10vh;
  flex: 1;
}

.highlightOrange {
  color: #ffb86c; 
  font-size: 1.2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.highlightRed {
  color: #ff5555; 
  font-size: 1.2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.highlightYellow {
  color: #f1fa8c; 
  font-size: 1.2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.highlightGreen {
  color: #ff79c6; 
  font-size: 1.2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}


.highlightTitle {
  color: #bd93f9; 
  font-size: 2.5rem;
  margin-bottom: 2.5rem;
  margin-top: 15vh;
}


.title {
  font-size: 2.5rem;
  color: #f8f8f3;
  margin-bottom: 7vh;
  margin-top: 10vh;
}

.subtitle {
  font-size: 1.2rem;
  color: #f8f8f3;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.section {
  margin-bottom: 2rem;
}

.sectionTitle {
  font-size: 1.1rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: white;
}

.socialCard {
  background-color: #252526;
  border: 1px solid #3c3c3c;
  padding: 12px 16px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  gap: 12px;
  color: #cccccc;
  text-decoration: none;
  margin-bottom: 12px;
  transition: background-color 0.2s ease;
}

.socialCard:hover {
  background-color: #333333;
  color: #ffffff;
}

.icon {
  font-size: 1.2rem;
}

.cardTitle {
  color: white;
  font-weight: bold;
}

.cardSubtext {
  font-size: 0.9rem;
  color: #aaa;
  margin-top: 4px;
}

.badge {
  font-size: 1.5rem;
  color: #3399ff;
}

.progressBar {
  height: 2px;
  background-color: #3399ff;
  width: 100%;
  margin-top: 8px;
}
