body {
  margin: 0;
  background-color: #282a36;
  color: #cccccc;
  font-family: 'Segoe UI', Tahoma, sans-serif;
}


.body {
  padding:20px;
  padding-top: 60px;
  background: #f2f0ef;
}

.editorWindow {
  background-color: #44475a;
  margin: 10px 15px;
  border: 1px solid #3c3c3c;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  color: #000000;
  height: calc(100vh - 30px); /* 100vh - navbar height */
  font-family: 'Segoe UI', Tahoma, sans-serif;
}
